<template>
  <v-container>
    <v-layout row>
      <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>
      <v-flex style="text-align:center">
        <!-- <v-img src="http://cdn.wallpapersafari.com/7/86/gqiGH7.jpg" height="50px" width="50px"> -->

        <v-container class="sober fill-height" fluid>
          <v-row align="center" justify="center">
            <v-col cols="12" sm="8" md="4">
              <img :src="require('@/assets/images/nepal-govt.png')" width="140px" />
              <h4>भवन निर्माण अनुमति प्रकृया</h4>
            </v-col>
          </v-row>

          <v-row align="center" justify="center">
            <v-col cols="12" sm="8" md="4">
              <v-card class="elevation-12">
                <v-toolbar color="primary" dark flat>
                  <v-toolbar-title>Building Permit System Login</v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                  <v-form>
                    <v-text-field
                      v-model="username"
                      name="username"
                      label="Username"
                      prepend-icon="mdi-account"
                    >
                    </v-text-field>
                    <v-text-field
                      v-model="password"
                      name="password"
                      label="Password"
                      type="password"
                      prepend-icon="mdi-lock"
                      v-on:keydown.enter="authenticate"
                    >
                    </v-text-field>
                  </v-form>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" v-on:click="authenticate">Login</v-btn>
                  </v-card-actions>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
          <v-row align="center" justify="center">
            <v-col cols="12" sm="8" md="4">
              <span v-if="message">
                <v-alert dense outlined type="error">{{ message }}</v-alert>
              </span>
            </v-col>
          </v-row>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Login",
  components: {},
  data() {
    return {
      username: "",
      password: "",
      token: "",
      message: "",
      isLoading: false
    };
  },

  mounted() {},

  methods: {
    authenticate() {
      this.isLoading = true;
      const url = "api/login";
      VueAxios.post(url, { username: this.username, password: this.password, remember: true })
        .then(response => {
          this.isLoading = false;
          if (response.status === 200) {
            this.token = response.data.access_token;
            window.localStorage.clear();
            window.localStorage.setItem("auth_user_role_id", response.data.data.user_role_id);
            // window.localStorage.setItem('auth_user_role_id', response.data.data.user_role_id);
            window.localStorage.setItem("token", response.data.access_token);

            // Set the VueAxios headers to new Authorization token.
            // Otherwise, it will contain old token from last login since the object is not destroyed
            // until browser close, leading to 401 unauthorized errors
            VueAxios.defaults.headers = {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${localStorage.getItem("token")}`
            };

            const currentuserUrl = "api/profile";
            VueAxios.get(
              currentuserUrl,
              {
                headers: {
                  "X-Requested-With": "XMLHttpRequest",
                  Authorization: `Bearer ${localStorage.getItem("token")}`
                }
              },
              {
                timeout: 100000
              }
            ).then(responseUser => {
              this.user = responseUser.data;
              window.localStorage.setItem("user", this.user);
            });
            this.$router.push({ name: "home" });
          }
        })
        .catch(() => {
          this.isLoading = false;
          this.message = "Either the username or password is incorrect. Please try again!";
        });
      // .catch(error => this.error = error.response.data.errors);
    }
  }
};
</script>
